@import "./Colors.scss";

.sortableUnit {
    display: flex;
    flex-direction: column-reverse;
}

.sortableUnit .rect {
    background: $SortableRect;
}

.sortableUnit .numLabel {
    text-align: center;
    color: $SortableRectNum;
    font-size: 1vw;
}

.rectIndex {
    display: flex;
    align-self: center;
    height: 20px;
    text-align: center;
    color: $SortableRectIndex;
}

.rectSpacer {
    display: flex;
    flex-grow: 1;
}