@import "./Colors.scss";

.side-drawer {
    position: relative;
    box-shadow: 5px 0 5px -1px  rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.side-drawer-header {
    height: 20%;
    background: $sideDrawerHeader;
    color: $sideDrawerTitle;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    overflow: hidden;
}
.side-drawer-header p {
    font-size: 1.8vw;
    text-align: center;
    text-decoration: none;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 2vw;
    margin-right: 2vw;
}

.titleSpacer {
    height: max-content;
}
.subtitle {
    height: 3vw;
    background: $sideDrawerButtonListTitleBackground;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    overflow: hidden;
}
.subtitle img{
    margin-left: 10px;
    align-self: center;
    width: 1.5vw;
    height: 1.5vw;
}
.subtitle span{
    align-self: center;
    color: $sideDrawerButtonListTitleText;
    height: 2vw;
    font-size: 1.2vw;
    margin-left: 0.3vw;
}

.side-drawer-buttonList {
    height: 94%;
    background:$sideDrawerBackground;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}